.button {
  position: relative;
  display: inline-block;
  width: 12.625rem;
  border: 0.0625rem solid #408F95;
  background-color: #408F95;
  color: #fff;
  transition: 0.3s ease;
  cursor: pointer;
  z-index: 3;
}

.button::before {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  display: inline-block;
  width: 12.625rem;
  height: 3.375rem;
  padding: 0 0.25rem 0.25rem 0;
  border: 0.0625rem solid #408F95;
  z-index: -1;
}

.button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover {
  background-color: #fff;
  color: #408F95;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button span {
  display: inline-block;
  width: 100%;
  padding: 1rem 2.5rem;
  font-size: 0.875rem;
  line-height: 1.571;
  text-align: left;
  font-family: "Noto Sans JP", sans-serif;
}

.button span::first-letter {
  text-transform: capitalize;
}

.button span::before,
.button span::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 2.1875rem;
  height: 0.0625rem;
  background-color: #fff;
  transition: 0.3s background-color;
}

.button span::before {
  width: 2.5rem;
  transform: translateY(-50%);
}

.button span::after {
  width: 0.575rem;
  transform: translateY(-50%) rotate(45deg);
  transform-origin: right center;
}

.button span:hover::before,
.button span:hover::after {
  background-color: #408F95;
}

.button.button-send span {
  padding: 1rem 3.5rem;
}

.button.button-white {
  background-color: #fff;
  color: #408F95;
  border: 0.0625rem solid #fff;
}

.button.button-white::before {
  border: 0.0625rem solid #fff;
}

.button.button-white::after {
  background: #408F95;
}

.button.button-white:hover {
  background-color: #408F95;
  color: #fff;
}

.button.button-white span::before,
.button.button-white span::after {
  background-color: #408F95;
}

.button.button-white span:hover::before,
.button.button-white span:hover::after {
  background-color: #fff;
}

