  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.375rem;
  height: 2.375rem;
  border: 1px solid #408F95;
  color: #408F95;
  font-size: 1rem;
  font-weight: 700;
  font-family: "Lato", sans-serif;
  transition: 0.3s background-color;
}

.pagination a:hover {
  background-color: #408F95;
  color: #fff;
}

