  max-width: 100%;
}

.sidebar-voice-card__img img {
  width: 100%;
  height: 100%;
  aspect-ratio: 294/218;
  object-fit: cover;
}

.sidebar-voice-card__body {
  margin-top: 1.125rem;
}

.sidebar-voice-card__character {
  font-size: 0.75rem;
  line-height: 1.5;
  font-weight: 400;
  font-family: "Noto Sans JP", sans-serif;
}

.sidebar-voice-card__title {
  margin-top: 0.5rem;
  font-size: 1.5rem;
  font-weight: 500;
  font-family: "Noto Sans JP", sans-serif;
}

.sidebar__category:not(:first-child) {
  margin-top: 5rem;
}